import React, { Component } from "react";
import { Divider, Steps, message, Popover, Drawer, Form, Button, Col, Row, Input, Select, DatePicker } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import FormProfileInfo from './form-profile-info.js';
import FormProfileDetails from './form-profile-details.js';
import FormSummary from './form-summary.js';
import Devices from './devices';


const { Option } = Select;
const { Step } = Steps;
const steps = [
    {
      title: "Profile Info",
      description: "Enter basic profile information.",
      content: <FormProfileInfo />
    },
    {
        title: "App restrictions",
        description: "Configure app and device restrictions.",
        content: <FormProfileDetails />
      },
      {
      title: "Deploy",
      description: "Deploy your settings or save.",
      content: <Devices/>
    }
  
  ];


const customDot = (dot, { status, index }) => (
  <Popover
    content={
      <span>
        step {index} status: {status}
      </span>
    }
  >
    {dot}
  </Popover>
);

export class FormInDrawer extends Component {
  state = { visible: false };

  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };
  constructor(props) {
    super(props);
    this.state = {
      current: 0
    };
  }

  next() {
    const current = this.state.current + 1;
    this.setState({ current });
  }

  prev() {
    const current = this.state.current - 1;
    this.setState({ current });
  }
  
  save(){
    this.onClose();
    message
    .loading("Saving profile..", 2.5)
    .then(() => message.success("Profile Saved", 2.5))
    .then(() => console.log("done"));

  }
  savedeploy(){
    this.onClose();
    message
    .loading("Saving profile..", 2.5)
    .then(() => message.success("Profile Saved", 2.5))
    .then(() => message.success("Deploying to devices", 2.5));

  }


  
  render() {
    const { current } = this.state;
    
    return (

      <div>
        <Button type="primary" onClick={this.showDrawer}>
          <PlusOutlined /> New Configuration Profile
        </Button>
        <Drawer
          title="Create a new enrollment profile"
          width={720}
          onClose={this.onClose}
          visible={this.state.visible}
          bodyStyle={{ paddingBottom: 80 }}
          footer={
            <div
              style={{
                textAlign: 'right',
              }}
            >
          {current < steps.length - 1 && (
            <Button type="primary" onClick={() => this.next()}>
              Next
            </Button>
          )}
          {current === steps.length - 1 && (
              
              <Button
                type="primary"
                onClick={() => this.savedeploy()}
              >
                Save &amp; Deploy
              </Button>
            )}          
          {current === steps.length - 1 && (
              
            <Button
              type="primary" style={{ marginLeft: 8 }}
              onClick={() => this.save()}
            >
              Save Only
            </Button>
          )}
          {current > 0 && (
            <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>
              Previous
            </Button>
          )}
            </div>
          }
        >
          <Steps size="small" current={current} progressDot={customDot}>
          {steps.map(item => (
              <Step key={item.title} title={item.title} title={item.description} />
            ))}

          </Steps>
          <Divider />
          <div className="steps-content">{steps[current].content}</div>
        </Drawer>
      </div>
    );
  }
}

export default FormInDrawer;
